import React, { Component } from "react";
import { MDBBadge, MDBContainer, MDBRow } from "mdbreact";

class LevelPage extends Component {
    state = {
      level: "1"
    };

    render() {
      return (
        <MDBContainer className="justify-content-center">
          <h5 className="text-center mb-0"><strong><strong>Level</strong></strong><br />
            <MDBBadge color={this.state.level==="1" ? "default" : "stylish-colork"}>1</MDBBadge>&nbsp;
            <MDBBadge color={this.state.level==="2" ? "default" : "stylish-color"}>2</MDBBadge>&nbsp;
            <MDBBadge color={this.state.level==="3" ? "default" : "stylish-color"}>3</MDBBadge>&nbsp;
            <MDBBadge color={this.state.level==="4" ? "default" : "stylish-color"}>4</MDBBadge>&nbsp;
            <MDBBadge color={this.state.level==="5" ? "default" : "stylish-color"}>5</MDBBadge>
          </h5>
        </MDBContainer>
      );
    }
}

export default LevelPage;
