import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import { MDBContainer, MDBRow, MDBCol, MDBBtn } from "mdbreact";
import LevelPage from '../LevelPage/LevelPage';
import ProgressbarPage from '../ProgressBar/ProgressBarPage';
import Total from '../Total/Total';
import StudyInfo from '../StudyInfo/StudyInfo';


class Dashboard extends Component {
  state = {
    redirected: false
  }

  redirecthandler = () => {
    this.setState({redirected: true});
  };

  render() { 
    if (this.state.redirected === true) {
      return <Redirect to='/playground' />
    }

    return (
      <MDBContainer>
        <MDBRow className="justify-content-center">
          <MDBCol md="4"><LevelPage /></MDBCol>
          <MDBCol md="4"><ProgressbarPage /></MDBCol>
          <MDBCol md="4"><Total /></MDBCol>
        </MDBRow>
        <br />
        <br />
        <MDBRow className="justify-content-center">
            <MDBBtn onClick={this.redirecthandler}>
              <strong>Continue training</strong>
            </MDBBtn>
        </MDBRow>
        <br />
        <MDBRow className="justify-content-center">
          <StudyInfo />
        </MDBRow>
      </MDBContainer>
    );
  };
}

export default Dashboard;
